@use 'colors';

@mixin nav-list {
    display: flex;
    align-items: center;
    height: 100%;
}

.site-nav {
    
    ul {
        @include nav-list;
        justify-content: space-between;
        gap: 1rem;
    }

    li {
        text-transform: capitalize;
    }

    li + li {
        border-left: 1px solid white;
        padding-left: 1rem;
    }
}

.external-nav {

    ul {
        @include nav-list;
    }

    @each $color in colors.$extNav {
        $i: index(colors.$extNav, $color);
        li:nth-child(#{$i}) {
            background-color: $color;
        }
    }

    li {
        $dimension: 5.25rem;
        height: $dimension;
        width: $dimension;
        padding: .5rem;
    }

    svg {
        fill: white;
        width: 100%;
        height: 100%;
    }
}